import React from "react";
import { IndexGreetingBlock, IndexGreetingButton, IndexGreetingButtons, IndexGreetingContent, IndexGreetingImage, IndexGreetingImageBottom, IndexGreetingStars, IndexGreetingStarsMobile, IndexGreetingSubtitle, IndexGreetingText, IndexGreetingTitle } from "./IndexGreetingStyles";
import Container from "../../../components/Common/Container/Container";
import { useNavigate } from "react-router-dom";
import FiltersService from "../../../services/filtersService";

const IndexGreeting = () => {
  const navigate = useNavigate();

  const toServices = () => {
    const services = document.querySelector("#services");
    services?.scrollIntoView({
      block: "start",
      behavior: "smooth",
    });
  };

  const toChildren = () => {
    FiltersService.setFilters({
      specialization: null,
      sex: null,
      age: null,
      height: null,
      hairColor: null,
      eyeColor: null,
      shoesSize: null,
      city: null,
      video: null,
    });

    navigate("/children");
  };

  return (
    <IndexGreetingBlock>
      <IndexGreetingStars src="/images/greeting-stars.png" />
      <IndexGreetingStarsMobile src="/images/greeting-stars-mobile.png" />
      <Container>
        <IndexGreetingContent>
          <IndexGreetingSubtitle> - </IndexGreetingSubtitle>
          <IndexGreetingTitle>Fresh casting</IndexGreetingTitle>
          <IndexGreetingText>  -  ,            . </IndexGreetingText>
          <IndexGreetingButtons>
            <IndexGreetingButton onClick={toServices}> </IndexGreetingButton>
            <IndexGreetingButton onClick={toChildren}> </IndexGreetingButton>
          </IndexGreetingButtons>

          <IndexGreetingImage src="/images/greeting.png" />
          <IndexGreetingImageBottom src="/images/greeting-bottom.png" />
        </IndexGreetingContent>
      </Container>
    </IndexGreetingBlock>
  );
};

export default IndexGreeting;
