import React, { FC, useMemo, useState } from "react";
import { ChildrenMobileFilterBlock, ChildrenMobileFilterBody, ChildrenMobileFilterButton, ChildrenMobileFilterClose, ChildrenMobileFilterContent, ChildrenMobileFilterTitle } from "./ChildrenMobileFilterStyles";
import { ChildrenChosenFilters, ChildrenFilters } from "../../../types/child";
import ChildrenMobileFilterSelect from "./ChildrenMobileFilterSelect/ChildrenMobileFilterSelect";
import ChildrenMobileFilterInput from "./ChildrenMobileFilterInput/ChildrenMobileFilterInput";

interface IChildrenMobileFilter {
  filters: ChildrenFilters;
  chosenFilters: ChildrenChosenFilters;
  onChange(filters: ChildrenChosenFilters): void;
  onClose(): void;
}

const ChildrenMobileFilter: FC<IChildrenMobileFilter> = ({ filters, chosenFilters, onChange, onClose }) => {
  const [temporaryChosenFilters, setTemporaryChosenFilters] = useState<ChildrenChosenFilters>(chosenFilters);

  const changeFilter = (filter: string, value: string | null) => {
    setTemporaryChosenFilters((prev) => {
      return {
        ...prev,
        [filter]: value,
      };
    });
  };

  function onFiltersChange() {
    onChange(temporaryChosenFilters);
    onClose();
  }

  const stopPropagation = (e: React.SyntheticEvent) => e.stopPropagation();

  return (
    <ChildrenMobileFilterBlock onClick={onClose}>
      <ChildrenMobileFilterContent onClick={stopPropagation}>
        <ChildrenMobileFilterTitle>
          <ChildrenMobileFilterClose onClick={onClose} src="/images/close.svg" />
          
        </ChildrenMobileFilterTitle>

        <ChildrenMobileFilterBody>
          <ChildrenMobileFilterSelect title="" value={temporaryChosenFilters.specialization} values={filters.specialization} onChange={(value) => changeFilter("specialization", value)} />
          <ChildrenMobileFilterSelect title="" value={temporaryChosenFilters.sex} values={filters.sex} onChange={(value) => changeFilter("sex", value)} />
          <ChildrenMobileFilterInput title="" value={temporaryChosenFilters.age} unit="" onChange={(value) => changeFilter("age", value)} />
          <ChildrenMobileFilterInput title="" value={temporaryChosenFilters.height} unit="" onChange={(value) => changeFilter("height", value)} />
          <ChildrenMobileFilterSelect title="" value={temporaryChosenFilters.hairColor} values={filters.hairColor} onChange={(value) => changeFilter("hairColor", value)} />
          <ChildrenMobileFilterSelect title="" value={temporaryChosenFilters.eyeColor} values={filters.eyeColor} onChange={(value) => changeFilter("eyeColor", value)} />
          <ChildrenMobileFilterSelect title="" value={temporaryChosenFilters.shoesSize} values={filters.shoesSize} onChange={(value) => changeFilter("shoesSize", value)} />
          <ChildrenMobileFilterSelect title="" value={temporaryChosenFilters.city} values={filters.city} onChange={(value) => changeFilter("city", value)} />
          <ChildrenMobileFilterSelect title="" value={temporaryChosenFilters.video} values={filters.video} onChange={(value) => changeFilter("video", value)} />
        </ChildrenMobileFilterBody>

        <ChildrenMobileFilterButton onClick={onFiltersChange}> </ChildrenMobileFilterButton>
      </ChildrenMobileFilterContent>
    </ChildrenMobileFilterBlock>
  );
};

export default ChildrenMobileFilter;
